import React from 'react'
import { View, Text,Button,StyleSheet,TextInput,Textarea} from 'react-native'

const InProgressProject = () => {
    
    const [value,onChangeText]=React.useState("")

    return (
        <>
            <View style={Styles.container2}>

                <Text>Mes projets</Text>
                <View style={Styles.myProject}>
                    <View style={Styles.buttonProject}>
                        <Button title='+' onPress={()=>{}}/>
                    </View>
                    {/* creation champs de formulaire */}
                    <View style={Styles.createForm}>
                        <Text>Nom du projet</Text>
                        <TextInput onChangeText={text => onChangeText(text)}
                                   value={value}
                                   style={Styles.champs}/>
                        <View style={Styles.buttonCreateP}>
                           <Button title='créer'/>
                        </View>
                        {/* <Text>Description du projet</Text>
                        <Textarea></Textarea> */}
                    </View>
                </View>
                
            </View>
        </>
    )
}

const Styles = StyleSheet.create({
    container2:{
        flex:1,
        padding:30,
    },
    myProject:{
        display:"flex",
   
    },
    buttonProject:{
       
       flexDirection:"row",
    },
    createForm:{
        display:"flex",
        border:"2px solid"
    },
    buttonCreateP:{
       flexDirection:"row"
    },
    champs:{
        
        backgroundColor:"#fff"
    }
})
export default InProgressProject
