import { StatusBar } from 'expo-status-bar';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import React from 'react';
import { Text, View } from 'react-native';
import HomePageScreen from './screen/HomePageScreen';
import InprogressProject from './screen/InProgressProject';
import FinishedProject from './screen/FinishedProject'

const Stack = createStackNavigator();

const App =() =>{
  return (
     <NavigationContainer>

       <Stack.Navigator>
          <Stack.Screen name='home' component={HomePageScreen}/>
          <Stack.Screen name='InProgressProject' component={InprogressProject}/>
          <Stack.Screen name='FinishedProject ' component={FinishedProject}/>
          
       </Stack.Navigator>
          
     </NavigationContainer>
    
  );
}



export default App